.search-excerpt {
  color: blue;
  background-color: yellow;
  font-size: 1.2em; }

h1, h2, h3, h4, h5, h6 {
  clear: both;
  font-family: "Dosis", sans-serif; }

h1 {
  color: #0a0c2d; }

h1.page-title.results-page.results-page {
  margin-left: 35px; }

h1 span.search-terms {
  color: #0a0c2d;
  background: yellow; }

h2.comments-title, h3.comment-reply-title, p.logged-in-as, p.comment-form-comment, p.form-submit {
  margin-left: 70px; }

.site-info {
