.size-mitema_full_size {
  width: 100%;
  overflow: hidden;
  height: auto; }

.imagen_mini {
  width: 40%;
  float: left;
  margin: 0 10px 0px 0;
  border: 2px solid #fff;
  border-radius: 5px; }

.caja {
  padding: 5px;
  background: #fff; }

figure {
  margin: 1em 0;
  /* Extra wide images within figure tags don't overflow the content area. */ }
  figure.imagen-destacada {
    margin: 0 0 1em 0;
    /*revisar a ver como queda*/ }

table {
  margin: 0 0 1.5em;
  width: 100%; }

article, #comments {
  margin: 20px;
  padding: 15px;
  background-color: #e8e8e8;
  border-radius: 8px; }

body.single article.tag-html {
  background-color: #ced7e6; }

article.tag-html h3.html-content {
  background-color: #0a0c2d;
  color: #fff;
  width: 250px;
  border-radius: 10px;
  padding: 4px 10px; }

h3.importante {
