        padding: 4px 10px; }
      header #site-navigation li.menu-item a:hover {
        background-color: transparent;
        color: #fff; } }

#masthead {
  background-color: #0a0c2d; }

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
footer.site-footer {
  font-size: 1.1em;
  background-color: #0a0c2d;
  margin: 20px 0 0; }
  footer.site-footer #footer-menu li a {
    color: #fff; }
  footer.site-footer .menu-footer-container > a {
    color: #fff;
    padding: 10px 0;
    display: block; }

footer footer.site-info {
  color: #fff; }

footer .menu-footer-container {
  text-align: center; }

footer div.footer {
  display: flex;
  justify-content: space-around;
  color: #fff; }

footer .menu-footer-container {
  text-align: center; }

footer #footer-menu li {
  list-style: none;
  display: inline-block;
  margin: 0 10px; }

footer #footer-menu a {
  text-decoration: none; }

footer ol {
  margin: 0;
  padding: 0; }

footer ul {
  background: transparent;
  padding: 10px 0;
  margin: 0; }

/*--------------------------------------------------------------
## Sidebar
--------------------------------------------------------------*/
.widget-area {
  margin-top: 20px;
  padding-right: 20px;
  font-size: 1em; }
  .widget-area ul {
    background: transparent;
    padding: 0;
    padding-right: 20px;
    margin: 0; }
  .widget-area h2 {
    margin-bottom: 5px; }
  .widget-area .widget_search form input {
    width: 100%;
    font-size: 1.2em;
    margin-bottom: 5px; }
  .widget-area .widget_search form button {
    width: 100%; }

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none; }

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
  display: block; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
  display: inline-block; }

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%; }
  .wp-caption img[class*="wp-image-"] {
    display: block;
    margin-left: auto;
    margin-right: auto; }
  .wp-caption .wp-caption-text {
    margin: 0.8075em 0; }

.wp-caption-text {
  text-align: center; }

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

/*--------------------------------------------------------------
# Sidebar structure
--------------------------------------------------------------*/
.content-area {
  float: left;
  margin: 0 -30% 0 0;
  width: 100%; }
  .content-area.sin-widget {
    width: 100%;
    float: none; }
    .content-area.sin-widget #main {
      margin: 0; }

.site-main {
  margin: 0 30% 0 0; }

.site-content .widget-area {
  float: right;
  overflow: hidden;
  width: 30%; }

.site-footer {
  clear: both;
  width: 100%; }

@media screen and (max-width: 37.5em) {
  .content-area {
    float: none;
    width: 100%; }
  .site-main {
    margin: 0; }
  .site-content .widget-area {
    float: none;
    margin: 20px auto;
    width: 90%; }
  .site-content .widget-area .widget {
    display: inline-block;
    vertical-align: top;
    border-top: 2px solid;
    border-color: #0a0c2d;
    padding-top: 10px;
    min-width: 45%; } }

.no-sidebar .content-area {
  float: none;
  margin-left: auto;
  margin-right: auto; }

.no-sidebar .site-main {
  margin-right: 0; }
